.body-insertnutritions{
    background-image: linear-gradient(135deg, rgb(5, 3, 97) 0%, rgba(24, 148, 185, 0.8) 100%);
    
}
.body-insertnutrition{
    width: 374px;
    margin: 0 auto;
    background-color: #e3e3e3;
    padding-bottom: 300px;
}
.title-insertnutrition{
    background-color: #4259ff;
    height: 50px;
    color: white;
    padding-top: 10px;
}
.title-insertnutrition h1{
    text-align: center;
    font-size: 20px;
    font-weight: 500;
}
.detial-insertnutrition {
    width: 100%;
    padding: 0;
}
.body-detialinsu{
    padding: 20px;
}

.detial-insertnutrition span{
    display:  flex;
    margin-bottom: 10px;
}
.detial-insertnutrition span h3{
    margin-top: 10px;
    width: 35%;
    font-weight: 700;
    font-size: 14px;
}

.detial-insertnutrition span input{
    width: 70%;
    height: 45px;
    border-radius: 15px;
    font-size: 16px;
    padding-left: 20px;
    border-style: none;
}
.detial-insertnutrition span select{
    width: 70%;
    height: 45px;
    border-radius: 15px;
    padding-left: 20px;
    font-size: 16px;
    border-style: none;
}

.detial-calI {
    width: 100%;
    background-color: white; 
    padding: 20px 10px;
    border-radius: 15px;
    margin: 20px 0 ;
}
.detial-calI span{
    display: flex;
    margin: 10px 0;
}
.dot{
    width: 6px;
    height: 6px;
    margin: auto 5px;
    border-radius: 10px;
    background-color: #fc6b00;
}
.detial-calI h1{
    padding-left: 10px;
    width: 40%;
    margin: auto 0;
    font-weight: 700;
    font-size: 14px;
}
.detial-calI input{
    width: 50%;
    text-align: center;
    border-radius: 15px;
    height: 35px;
}
.ssss{
    font-size: 14px;
    font-weight: 700;
}
.btn-page{
    position: fixed;
    width: 374px;
    text-align: center;
    bottom: 80px;
}
.btn-page button{
    width: 40%;
    height: 50px;
    margin: 5px;
}
.btn-page button:nth-of-type(1){
    border-radius: 15px;
    color: #4259ff;
    border: 1px solid #4259ff;
}
.btn-page button:nth-of-type(2){
    border-radius: 15px;
    color: #ffffff;
    background-color: #4259ff;
    border-style: none;
    
}
@media only screen and (max-width: 600px){

    .body-insertnutrition{
        width: 100%;
    }
    .btn-page{
        width: 100%;
    }
}