.registor-admin{
    width: 374px;
    text-align: center;
    margin: auto;
    padding: 20px 5px;
    background-color: #4259ff;    
}
.registor-admin h1{
    margin-top: 10px;
    font-size: 24px;
    color: white;
    font-weight: bold;
    margin-bottom: 10px;
}
.rgt-am-1 p{
    margin: 10px;
    color: white;
    font-size: 18px;
}
.rgt-am-data{
    text-align: start;
    margin-top: 20px;
    height: auto;
    width: 100%;
    padding: 15px;
    margin-bottom: 20px;
    background-color: rgba(114, 113, 113, 0.3);
    border-radius: 10px;
}
.rgt-am-1 input{
    margin-bottom: 15px;
}
@media only screen and (max-width: 600px){
    .registor-admin {
      width: 100%;
    }
}