
.body-navbar{
    width: 374px;
    margin-left: auto;
    margin-right: auto;
}
.nav-bars{
    display: flex;
    width: 374px;
    position: fixed;
    bottom: 0;
    z-index: 1;
    padding: 10px 0;
    background-color: white;
}
.nav-bars p{
    color: rgb(61, 61, 61);
}
.icon-homebar{
    width: 33.33%;
    height: 50px;
    text-align: center;
    cursor: pointer;
}
.icon-homeL{
    margin-left: auto;
    margin-right: auto;
    width: 31px;
    height: 28px;
    margin-bottom: 3px;
    background-image: url('../../img/login/nav/home.svg');
}
.icon-homeL{
    margin-left: auto;
    margin-right: auto;
    width: 31px;
    height: 28px;
    margin-bottom: 3px;
    background-image: url('../../img/login/nav/home.svg');
}
.icon-menubar{
    width: 33.33%;
    height: 50px;
    text-align: center;
    cursor: pointer;
}
.icon-menuL{
    margin-left: auto;
    margin-right: auto;
    width: 25px;
    height: 30px;
    margin-bottom: 1px;
    background-image: url('../../img/login/nav/menu.svg');
}.icon-nutrorionbar{
    width: 33.33%;
    height: 50px;
    text-align: center;
}
.icon-nutrorionL{
    margin-left: auto;
    margin-right: auto;
    cursor: pointer;
    width: 30px;
    height: 31px;
    background-image: url('../../img/login/nav/nutrition.svg');
}
@media only screen and (max-width: 600px){
    .body-navbar{
        width: 100%;
    }
    .nav-bars{
        width: 100%;
    }
}