.body-ingredients{
    background-image: linear-gradient(135deg, rgb(5, 3, 97) 0%, rgba(24, 148, 185, 0.8) 100%);
}
.body-ingredient{
    width: 374px;
    text-align: center;
    background-color: #e3e3e3;
    margin: -20px auto;
    padding-bottom: 900px;
}
.title-ingredient{
    position: relative;
    width: 100%;
    background-color: #4259ff;
    height: 100px;
    padding-top: 20px;
    border-radius: 0 0 10px 10px;
    margin-bottom: 40px;
}
.body-ingredients h1{
    margin: 20px;
    font-size: 20px;
    color: white;
    font-weight: 600;
}
.data-ingredient{
    width: 90%;
    background-color: white;
    margin: 10px auto;
    border-radius: 15px;
    padding: 10px;
    text-align: start;
}
.data-ingredient span{
    display: flex;
}

.data-ingredient h3{
    margin-right: 5px;
    font-weight: 600;
}
.data-ingredient p{
    flex-grow: 1;
}
@media only screen and (max-width: 600px){
    .body-ingredient{
        width: 100%;
    }

}