.body-foods{
    background-image: linear-gradient(135deg, rgb(5, 3, 97) 0%, rgba(24, 148, 185, 0.8) 100%);
    
}
.body-food{
    margin: 0px auto;
    width: 374px;
    padding-bottom: 500px;
    background-color:#E3E3E3;

}
.title-foods{
    width: 100%;
    height: 50px;
    padding-top: 10px;
    text-align: center;
    background-color: #4259ff;
}
.title-foods div{
    color: white;
    height: 30px;
    font-weight: 600;
    margin: auto;
}
.item-food{
    background-color:#E3E3E3;
}
.img-food{
    border-radius: 20px;
    width: 100%;
    height: 250px;
}
.img-food img{
    width: 100%;
    height: 250px;
    border-radius: 0 0 10px 10px;
}
.detial-food{
    width: 100%;
    margin-top: 20px;
    padding: 0 20px;
}
.title-food{
    display: flex;
}
.title-food div{
    flex-grow: 1;
    font-weight: bold;
    font-size: 18px;
    color: black;
}
.title-food button{
    font-size: 14px;
    border-radius: 15px;
    color: #4259ff;
    border: solid #4259ff;
    width: 60px;
    font-weight: 600;
}
.cal-food{
    display: flex;
    margin-top: 15px;
}
.cal-food span{
    background-color: #4259ff;
    width: 70px;
    margin: auto;
    height: 70px;
    padding: 5px;
    border-radius: 15px;
    text-align: center;
}
.cal-food span p{ 
    color: white;
    font-size: 14px;
    margin-top: 0px;
    line-height: 20px;
}
.material-food{
    margin-top: 20px;
    padding: 10px;
    border-radius: 10px;
    background-color: rgb(255, 255, 255);
}
.material-food h4{
    font-weight: bold;
    margin-bottom: 10px;
}
.material-items{
    display: flex;
}
.material-items div{
    font-size: 14px;
}
.sol-food{
    margin-top: 20px;
    padding: 10px;
}
.sol-food h4{
    font-weight: 600;
    margin-bottom: 10px;
    border-radius: 10px;
    font-size: 16px;
}
.sol-food p{
    font-size: 14px;
    margin-left: 0px;
    color: rgb(0, 0, 0);
}
.material-pont{
    background-color: #fc6b00;
    width: 5px;
    border-radius: 5px;
    height: 5px;
    margin: 9px 5px;
}
.n-foods{
    flex-grow: 1;
}
.na-foods{
    width: 40px;
}
.u-foods{
    width: 20px;
}

@media only screen and (max-width: 600px){
    .body-food{
        width: 100%;
    }

}
