.registor-store{
    width: 374px;
    margin: auto;
    padding: 5px;
    text-align: center;
    background-color: #4259ff;    
}
.registor-store h1{
    margin-top: 20px;
    font-size: 32px;
    color: white;
    font-weight: bold;
}
.rgt-data{
    margin-top: 20px;
    width: 100%;
    padding: 10px;
    text-align: start;
    background-color: rgba(114, 113, 113, 0.3);
    border-radius: 10px;
}
.rgt-data p{
    color: white;
    font-size: 18px;
    margin: 10px 5px;
}
.rgt-1{
}
.rgt-2{
    margin-left: auto;
}
.ip-rgt{
    margin-bottom: 10px;
    width: 100%;
}
.btn-rgt{
    margin-top: 20px;
    justify-content: center;
    text-align: center;
}
.rgt-lg{
    width: 40%;
}
@media only screen and (max-width: 600px){
    .registor-store {
      width: 100%;
    }
}
