.body-app1{
  background-image: linear-gradient(135deg, rgb(5, 3, 97) 0%, rgba(24, 148, 185, 0.8) 100%);

}
.App1 {
  border-radius: 10px;
    margin: 0 auto;
    padding: 0;
    width: 374px;
    box-sizing: border-box;
    text-align: center;
    background-color: #ffffff;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: rgb(0, 0, 0);
    
  }
  
.Body1 {
    align-items: center;
  }

  .text1 {
    font-family: "ThaiSansNeue", Times, serif;
  }

.text2{
    bottom: 0;
    margin:20px;
    text-align: center;
    display: flex;
    line-height: 75px;
  }
  
.dotGray {
    height: 10px;
    width: 10px;
    background-color: #bbb;
    border-radius: 50%;
    margin: 5px;
  }

.dotBlue {
    height: 10px;
    width: 10px;
    background-color: rgb(34, 97, 192);
    border-radius: 50%;
    margin: 5px;
}
.btn-page-start{
  margin-top: 20px;
  width: 100%;
}
.btn-start{
    border-radius: 20px;
    border-style: solid;
    border-color: #4259ff;
    width: 40%;
    background-color: white;
    margin-right: 10px;
    font-size: 16px;
    font-weight: 600;
    height: 50px;
    color: rgb(34, 97, 192);
}
.btn-next{
  border-radius: 20px;
  border-style: none;
  width: 40%;
  background-color: #4259ff;
  margin-left: 10px;
  color: white;
  font-size: 16px;
  font-weight: 600;
  height: 50px;
}
.btn-start-login{
  border-radius: 20px;
  border-style: none;
  width: 90%;
  background-color: #4259ff;
  color: white;
  font-size: 16px;
  font-weight: 600;
  height: 50px;
}
 
@media only screen and (max-width: 600px){
  .App1 {
    width: 100%;
  }
}