
.body-home{
    background-image: linear-gradient(135deg, rgb(5, 3, 97) 0%, rgba(24, 148, 185, 0.8) 100%);
}
.contianner {
    width: 374px;
    margin-left: auto;
    margin-right: auto;
    background-color: #f3f3f3;
    padding-bottom: 500px;
}
.home-title{
    background-color: #4259ff;
    display: flex;
    position: relative;
    justify-items: center;
    height: 150px;
    width: 100%;
    border-radius: 10px;
    border-top-left-radius: 0;
    border-top-right-radius: 0;
    padding: 0 20px;
}
.logo{
    color: white;
    width: 15%;
    height: 100px;
    font-size: 50px;
    margin: auto;
}
.title-logo{
    margin: auto;
    width: 80%;
    color: white;
    font-size: 18px;
    font-weight: 500;
    display: flex;
}
.sea-text{
    position: absolute;
    width: 340px;
    left: 17px;
    height: 40px;
    border-style: none;
    border-radius: 10px;
    bottom: -20px;
    z-index: 0;
}
.borderin{
    border-radius: 10px;
}
.search-i{
    border-radius: 10px;
    font-size: 20px;
    padding: 7px;
    color: white;
    z-index: 0;
    background-color: #4259ff;
}
/* ---------end title---------- */
.title-detial{
    margin-top: 40px;
    padding: 0 20px;
    display: flex;
}
.title-detial1{
    font-size: 16px;
    font-weight: 600;
}
.title-detial2{
    margin-left: auto;
    font-size: 14px;
}
/* ---------end title---------- */
.cards::-webkit-scrollbar{
    width: 10px;
}

.cards { 
    margin-top: 10px;
    overflow-x: auto;
    overflow-y: hidden;
    white-space: nowrap;
    padding: 0 10px;
    margin-bottom: -40px;
}
.cardfoods {
    margin: 5px;
    max-width: 160px;
    height: 190px;
    display: inline-block;
    overflow-x: hidden;
    overflow-y: hidden;
    border-radius: 10px;
}
.cardfoods h4{
    margin-top: 5px;
    font-size: 12px;
    padding-left: 5px;
    color: #FC6B00;
    font-weight: 600;
}
.cardfoods p{
    font-weight: 600;
    font-size: 12px;
    line-height: 20px;
    padding-left: 5px;
    color: rgb(133, 132, 132);
}
.cardfoods img{
    width: 100%;
    height: 80%;
    border-radius: 15px;
}
/* --------endcard-------- */
.icon-area{
    margin-top: 0px;
    padding: 0 20px;
    text-align: center;
}
.icon-item{
    text-align: center;
    width: 25%;
    height: 100%;
    margin-left: 3px;
    margin-top: 30px;
    cursor: pointer;
}
.icon-item img{
    border-radius: 15px;
    width: 75px;
    height: 75px;
}
.icon-item p{
    margin-top: -8px;
    line-height: 0px;
    font-size: 14px;
    font-weight: 600;
}
.icon-area1{
    display: flex;
}
.icon-area2{
    display: flex;
}
/*----------------*/

.dropdown i{
    border: solid white;
    padding: 5px;
    font-size: 22px;
    border-radius: 20px;
    cursor: pointer;
}
.dropdown input{
    display: none;
}
/*---------------*/
.dropdown{
    top: 25px;
    position: absolute;
    right: 20px;
    height: 60px;
    display: inline-block;
    font-size: 16px;    
    color: white;
    width: 200px;
    text-align: end;
}
input[type="checkbox"]{
    display: none;
}
.dropdown label{
    box-sizing: border-box;
    display: inline-block;
    width: 60px;
    height: 100%;
    padding: 15px 5px;
    color: white;
    cursor: pointer;
    text-align: center;
    user-select: none;
}
.dropdown ul {
    position: absolute;
    list-style: none;
    text-align: start;
    width: 100%;
    background-color: white;
    z-index: 3;
    margin: 0;
    padding: 0;
    box-shadow: 0 1px 1px rgba(0, 0, 0, 0.3);
    display: none;
}
.dropdown ul li{
    padding: 10px 5px;
    background-color: #fff;
    color: #57a0d4;
    margin-bottom: 1px;
    cursor: pointer;
    width: 90%;
}
.dropdown ul li:hover{
    color: rgb(145, 11, 11);
}
.dropdown ul li a{
    color: inherit;
    text-decoration: none;
}
input[type="checkbox"]:checked ~ ul{
    display: block;
}
.title-detial2:hover{
    cursor: pointer;
    color: #4259ff;
}
@media only screen and (max-width: 600px){
    .contianner {
        width: 100%;
    }
    .sea-text{
        width: 90%;
        left: 5%;
        height: 40px;
        border-style: none;
        border-radius: 10px;
        bottom: -20px;
    }
    .icon-item p{
        margin-left: -10px;
    }
    .icon-item{
        width: 25%;
        margin-left: 10px;
    }
}

