.body-getfoods{
    background-image: linear-gradient(135deg, rgb(5, 3, 97) 0%, rgba(24, 148, 185, 0.8) 100%);
}
.body-getfood{
    width: 374px;
    margin: 0 auto;
    background-color: #e3e3e3;
    padding-bottom: 500px;
}
.body-detialgetfood{
    padding: 20px;
}
.title-getfood{
    background-color: #4259ff;
    height: 50px;
    text-align: center;
    padding-top: 10px;
    color: rgb(255, 255, 255);
}
.title-getfood h1{
    margin: auto;
    font-size: 20px;
    font-weight: 500;
}
.title-f{
    margin: 10px 0;
    font-weight: 700;
}
.sub-f{
    margin: 10px 0;
    font-weight: 700;
    font-size: 14px;
}
.cal-getfoods{
    display: flex;
    width: 100%;
    padding: -5px;
}

.cal-getfoods span{
    background-color: #4259ff;
    width: 25%;
    margin: 5px;
    padding: 5px;
    border-radius: 10px;
    text-align: center;
    color: white;
}
.cal-getfoods span p{ 
    font-size: 14px;
}
.cal-getfoods span h1{
    font-size: 20px;
    font-weight: 700;
}

.hr-getfood{
    width: 100%;
    height: 1px;
    background-color: rgb(201, 201, 201);
    margin: 15px auto;
}
.data-rdi{
    margin: auto;
    padding: 20px 10px;
    width: 100%;
    background-color: white;
    border-radius: 15px;
    color: black;
}
.data-rdi div{
    width: 100%;
    display: flex;
    margin: auto;
    height: 30px;
}
.data-rdi div h6{
    margin-top: auto;
    font-size: 14px;
    font-weight: 500;
    margin-bottom: auto;
    text-align: end;
    width: 30%;
}
.data-rdi div progress{
    height: 15px;
    margin: auto 5px;
    width: 160px;
}
.data-rdi div p{
    padding-left: 5px;
    margin-top: auto;
    margin-bottom: auto;
    font-size: 14px;
    font-weight: 500;
    width: 10%;
}
.data-rdi h1{
    width: 40px;
    margin-left: auto;
    font-size: 14px;
    font-weight: bold;
    margin-bottom: 15px;
}
.rdi-box{
    margin: 10px 0;
    width: 100%;
    font-size: 14px;
    color: rgb(117, 117, 117);
}
@media only screen and (max-width: 600px){
    .body-getfood{
        width: 100%;
    }

}
