
.body-nutritions{
    padding: 0;
    margin: 0;
    background-image: linear-gradient(135deg, rgb(5, 3, 97) 0%, rgba(24, 148, 185, 0.8) 100%);
    
}
.body-nutrition{
    width: 374px;
    margin: 0 auto;
    background-color: #e3e3e3;
    text-align: center;
    padding-bottom: 900px;
}
.title-nutrition{
    position: relative;
    background-color: #4259ff;
    width: 100%;
    padding: 10px;
    text-align: center;
    height: 50px;
}
.body-searchnutrition{
    background-color: white;
    padding: 20px 20px 50px 20px;
    border-radius: 0 0 10px 10px;

}
.sea-nutrition{
    top: 60px;
}
.title-nutrition h1{
    margin: auto;
    font-size: 20px;
    color: white;
}
.head-nutrition{
    width: 350px;
    bottom: 80px;
}
.head-nutrition button{
    border-radius: 15px;
    border-style: none;
    padding: 10px;
    width: 320px;
    background-color: #4259ff;
    color: white;
    position: fixed;
    bottom: 90px;
}
.result-search{
    text-align: start;
    width: 100%;
    font-weight: 900;
    margin: 20px 20px 10px 0;
}
.result-search h2{
    width: 90%;
    margin-left: 10%;
    margin-bottom: 20px;
    font-weight: 700;
    font-size: 20px;
}
.hr-nutrition{
    background-color: #e3e3e3;
    width: 100%;
    height: 1px;
    border-radius: 10px;
    margin-top: 45px;
}
.item-search {
    width: 90%;
    margin: 10px 5%;
    padding: 15px 30px;
    text-align: start;
    font-size: 16px;
    border-radius: 10px;
    background-color: rgb(255, 255, 255);
    color: black;
    cursor: pointer;
}
.item-search a{
    text-decoration: none;
}
.item-search:hover {
    color: #4259ff;
}

@media only screen and (max-width: 600px){
    .body-nutrition{
        width: 100%;
    }


}