.registor{
    width: 374px;
    margin: 0 auto;
    padding-left: 0;
    padding-bottom: 0;
    padding-right: 0;
    background-image: url('../../img/login/pagestartlogin.png');
     
}
.registor-type0{
    padding-top: 20px;
    text-align: center;
    margin-top: 430px;
    width: 100%;
    border-radius: 10px;
    border-bottom-left-radius: 0;
    border-bottom-right-radius: 0;
    background-color: white;
    color: #4259ff;
}
.registor-type p{
    font-weight: 900;
    font-size: 20px;
    color: #4259ff;
}
.btn-rg-store{
    font-weight: 500;
    font-size: 18px;
    width: 80%;
    background-color: #4259ff;
    color: white;
    border-style: none;
    height: 40px;
    border-radius: 10px;
    margin: 20px 0;
}
.btn-rg-admin{
    font-weight: 500;
    font-size: 18px;
    width: 80%;
    padding: 10px 0;
    background-color: #4259ff;
    color: white;
    border-style: none;
    border-radius: 10px;
    margin: 20px 0;
}
.iconrg{
    color: #4259ff;
    font-size: 40px;
}
i{
    margin: 20px 0;
}
@media only screen and (max-width: 600px){
    .registor {
      width: 100%;
    }
}