.body-manages{
    background-image: linear-gradient(135deg, rgb(5, 3, 97) 0%, rgba(24, 148, 185, 0.8) 100%);
}
.body-manage{
    width: 374px;
    margin: 0 auto;
    background-color: #e3e3e3;
    padding-bottom: 100px;

}
.title-manage-item-head{
    position: relative;
    width: 100%;
    background-color: #4259ff;
    height: 60px;
    padding-top: 20px;
    text-align: center;
    color: white;
    font-weight: 900;
}
.title-manage{
    display: flex;
    position: relative;
    padding: 10px;
}
.title-manage-imgs{
    width: 140px;
}
.title-manage-imgs img{
    margin-top: 10px;
    width: 120px;
    height: 120px;
    border-radius: 15px;
}
.choose-file{
    display: flex;
    position: absolute;
    bottom: 20px;
    left: 10px;
    padding: 5px;
    background-color: rgba(0, 0, 0, 0.6);
    color: white;
    width: 120px;
    height: 30px;
    padding-left: 15px;
    text-align: center;
    border-radius: 0 0 15px 15px;
    font-size: 14px;
}
.choose-file label{
    cursor: pointer;
}
.choose-file i{
    margin: 0;
    padding: 0;
}
.title-manage-item-coin{
    width: 220px;
    padding: 10px;
    margin: auto;
}
.txt-food{
    margin: 5px 0;
    border-radius: 15px;
    height: 50px;
    border-style: none;
    padding-left: 20px;
    width: 200px;
}
.cal-manage{
    margin-top: -10px;
    display: flex;
    padding: 10px;
}
.cal-manage span{
    width: 25%;
    text-align: center;
    border-radius: 15px;
    margin: 5px;
    background-color: #4259ff;
    color: white;
    font-size: 14px;
    padding: 5px;
}
.cal-manage h1{
    margin: 2px 0;
    font-weight: 600;
    font-size: 18px;
}
.hrx{
    width: 90%;
    height: 2px;
    margin: auto;
    background-color: rgb(204, 202, 202);
}
.result{
    display: flex;
    padding: 10px;
}
.result div:nth-of-type(1){
    color: black;
    font-weight: 700;
}
.result div:nth-of-type(2){
    margin-left: 10px;
    color: rgb(124, 124, 124);
}
.btn-insertname{
    display: flex;
    color: #4259ff;
    text-align: center;
    width: 50%;
    margin: auto;
    cursor: pointer;
}

.btn-insertname i{
    margin: 6px 0 auto;
    margin-right: 5px;
    padding: 0px;
    border-radius: 20px;
}

.material-manage{
    padding: 10px;
}
.detial-mage{
    background-color: white;
    padding: 15px;
    border-radius: 15px;
    position: relative;
    margin-bottom: 10px;
}
.detial-mage p{
    font-size: 14px;
    font-weight: 700;
    color: #949494;
    margin: 10px;
}
.btn-dede{
    position: absolute;
    color: #fcbb00;
    right: 10px;
    top: -5px;
    cursor: pointer;
}.detial-mage p span i{
    margin-left: auto;
    margin-right: 5px;
}
.detial-mage div div:nth-of-type(2){
    margin-right: -5px;
}
.detial-mage div div{
    display: block;
}
.sol-foods{
    padding: 10px;
}
.sol-foods h6{
    font-weight: 600;
    margin-bottom: 10px;
}
.btn-save{
    padding: 10px;
    text-align: center;
}
.btn-save button:nth-of-type(1){
    margin-right: 10px;
    width: 40%;
    border-radius: 15px;
    height: 40px;
    color: #4259ff;
    border:1px solid #4259ff;
    font-weight: 600;
}
.btn-save button:nth-of-type(2){
    margin-right: 10px;
    width: 40%;
    border-radius: 15px;
    height: 40px;
    color: #ffffff;
    background-color: #4259ff;
    border-style: none;
    font-weight: 600;
}
.type-in{
    display: block;
}
.type-inX{
    display: flex;
}
.sub{
    width: 97%;
}
.subx{
    width: 120px;
}
@media only screen and (max-width: 600px){
    .body-manage{
        width: 100%;
    }
    .btn-dede{
    }
}