.container-login{
    background: radial-gradient(circle, #0288D1, rgb(2, 50, 88));

}
.login{
    width: 374px;
    margin: 0 auto;
    background-image: url('../../img/login/pagestartlogin.png');
    display: block;
    padding: 0;
    position: relative;
}
.btn-fg{
    height: 430px;
}
.btn-login0{
    display: block;
    width: 374px;
    background-color: rgb(255, 255, 255);
    border-radius: 10px;
    border-bottom-left-radius: 0px;
    border-bottom-right-radius: 0px;
}
.btn-loginall {
    padding: 15% 0px;
    width: 100%;
}
.btn-loginall button{
    max-width: 1000px;
    border-radius: 10px;
    height: 50px;
    margin: 5px 5px;
    padding-left: 70px;
}
.btn-loginall span{
    margin-left: 0px;
}
.btn-loginall input {
    margin-left: 0px;
}
.btn-loginall label{
    margin-left: 0px;
}
.firebaseui-form-actions button{
    padding-left: 20px;
}
@media only screen and (max-width: 600px){
    .login {
      width: 100%;
    }
    .btn-login0{
        width: 100%;
    }
}