
.body-menus{
    background-image: linear-gradient(135deg, rgb(5, 3, 97) 0%, rgba(24, 148, 185, 0.8) 100%);
}
.body-menu{
    margin: 0px auto;
    width: 374px;
    text-align: center;
    background-color: #e3e3e3;
    padding-bottom: 500px;
}

.head-menu{
    width: 100%;
    position: relative;
    height: 60px;
    background-color: #4259ff;
    text-align: center;
    padding-top: 15px;
}
.title-menu{
    margin:auto;
    width: 50%;
    color: white;
    font-size: 20px;
    font-weight: 600;
}
.menu-sea{
    bottom: -50px;
}
.btn-insertfood{
    cursor: pointer;
    width: 350px;
    position: fixed;
    bottom: 80px;
    margin-left: 1%;
    text-align: center;
    z-index: 1;
    padding: 10px 0;
    color: white;
    border-radius: 15px;
    background-color: #4259ff;
}
/* ------------endhead---------- */
.results-menu{
    margin-top: 10px;
    text-align: start;
    position: relative;
}
.results-menu span:nth-of-type(1){
    font-weight: 700;
}
.results-menu span:nth-of-type(2){
    color: #fc6b00;
}
.results-menu span:nth-of-type(3){
    background-color: white;
    padding: 5px 10px;
    border-radius: 15px;
    position: absolute;
    right: 10px;
    bottom: -2px;
}
.results-menu span span:nth-of-type(2){
    margin-left: 5px;
}

.list-item{
    margin-top: 50px;
    padding: 10px;
}
.all-items{
    position: relative;
    cursor: pointer;
    margin-top: 20px;
    background-color: rgb(255, 255, 255);
    margin-bottom: -10px;
    border-radius: 15px;
}
.list-menu{
    display: flex;
    height: 120px;
    width: 100%;
    position: relative;
}
.list-menu img{
    border-radius: 15px 0 0px 15px;
    width: 120px;
    margin-right: 8px;
}
.list-menu span h4{
    font-size: 15px;
    margin: 5px 0;
    font-weight: bold;
    text-align: start;
}
.list-menu span div{
    display: flex;

}
.list-menu span div h6{
    font-size: 14px;
    width: 55px;
    line-height: 20px;
    text-align: start;
}

.list-menu span div p{
    font-size: 12px;
    width: 100px;
    margin: auto;
}
.ins-gda{
    margin-left: -5px ;
    background-color: white;
    color: #4259ff;
    border-radius: 10px;
    font-size: 12px;
    font-weight: bold;
    border-color: #4259ff;
    border-style:ridge;
    width: 80px;
}
.list-menu span div progress{
    width: 95px;
    height: 10px;
    margin: auto 0;
}
.kcal{
    position: absolute;
    background-color: #4259ff;
    color: white;
    line-height: 20px;
    border-radius: 10px;
    padding: 5px;
    top: 25px;
    width: 50px;
    right: 8px;
    text-align: center;
    font-weight: 900;

}
.kcal p:nth-of-type(2){
    font-size: 12px;
    font-weight: 400;
}
.gda-menu{
    margin-top: 2px;
}
.btn-deleteFood-food{
    background-color: #ff4242;
    color: #ffffff;
    border-radius: 10px;
    font-size: 12px;
    margin-left: 5px;
    border: none;
    font-weight: bold;
    width: 40px;
}
@media only screen and (max-width: 600px){
    .body-menu{
        width: 100%;
    }
    .btn-insertfood{
        width: 90%;
        margin-left: 5%;
    }
    .results-menu span:nth-of-type(3){
        bottom: -1px;
    }
}