.body-showingredie{  
    background-image: linear-gradient(135deg, rgb(5, 3, 97) 0%, rgba(24, 148, 185, 0.8) 100%);
}
.title-showingredie{
    width: 374px;
    margin-left: auto;
    margin-right: auto;
    background-color: #e3e3e3;
    padding-bottom: 900px;
    text-align: center;
    font-size: 28px;
    color: white;
    font-weight: 500;
}
.all-item{
    margin: auto;
}
.item-ing{
    text-align: start;
    margin-left: 10%;
    font-size: 16px;
    width: 100%;
    background-color: rgb(255, 255, 255);
    margin: 8px 0;
    padding: 10px 30px;
    color: #000000;
    border-radius: 10px;
}
.item-ing:hover{
    cursor: pointer;
    color: #4259ff;
}
@media only screen and (max-width: 600px){
    .title-showingredie {
      width: 100%;
    }
}