.body-MenuStores{
    background-image: linear-gradient(135deg, rgb(5, 3, 97) 0%, rgba(24, 148, 185, 0.8) 100%);
}
.body-MenuStore{
    width: 374px;
    margin: 0 auto;
    text-align: center;
    background-color: #e3e3e3;
    padding-bottom: 500px;
}
.title-MenuStore{
    position: relative;
    width: 100%;
    background-color: #4259ff;
    height: 100px;
    padding-top: 25px;
    border-radius: 0 0 10px 10px;
    margin-bottom: 40px;
}
.body-MenuStore h1{
    color: white;
    font-weight: 600;
    font-size: 26px;
}
.result-MenuStore{
    padding: 0 10px;
    margin-top: 0px;
    text-align: start;
    width: 100%;
    margin-left: auto;
    margin-right: auto;
}
.result-MenuStore span{
    display: flex;
}
.result-MenuStore h2{
    font-size: 20px;
    font-weight: 600;
    flex-grow: 1;
    margin-bottom: 10px;
}
.result-MenuStore h3{
    font-size: 14px;
    margin-top: 5px;
    margin-right: 5px;
    font-weight: 400;
}
.storeName-map{
    display: flex;
    background-color: #4259ff;
    width: 100%;
    border-radius: 10px;
    margin-top: 2%;
    padding: 2% 5%;
    cursor: pointer;
    color: rgb(255, 255, 255);
}
.storeName-map p{
    font-size: 20px;
    margin-right: 5%;
}
.storeName-map:hover{
    background-color: rgb(255, 255, 255);
    color: rgb(14, 161, 194);
    border: 2px solid rgb(14, 161, 194);
}
@media only screen and (max-width: 600px){
    .body-MenuStore{
        width: 100%;
    }

}
